.header {
  font-size: 12px;
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  z-index: 10;
  .logo {
    @include response(900px) {
      margin: 0 auto;
    }
  }
  &-top {
    text-align: center;
    padding: 8px 0;
    background: $main-70;
    color: #fff;
    a {
      color: #fff;
      text-decoration: none;
      font-weight: 700;
    }
    @include response(900px) {
      height: 42px;
      display: flex;
      align-items: center;
    }
  }
  &-inner {
    background: $main;
    padding: 12px 0;
    position: relative;
    z-index: 2;
    .container {
      display: flex;
      align-items: center;
      justify-content: space-between;
    }
    &__left,
    &__right {
      display: flex;
      align-items: center;
    }
    &__left {
      gap: 24px;
      @include response(900px) {
        width: 100%;
      }
    }
    &__right {
      gap: 32px;
    }
    &__item {
      text-decoration: none;
      color: #fff;
      font-weight: 700;
      text-transform: uppercase;
      display: flex;
      align-items: center;
      gap: 6px;
      cursor: pointer;
      @include transition();
      @include response(1000px) {
        display: none;
      }
    }
  }
  &-cart-btn {
    background: transparent;
    border: none;
    position: relative;
    &__marker {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 16px;
      height: 16px;
      font-size: 11px;
      border-radius: 50%;
      background: $accent;
      color: #fff;
      font-weight: 700;
      position: absolute;
      right: 0;
      top: -2px;
      z-index: 2;
    }
  }
  &-nav {
    background: #fff;
    border-bottom: 1px solid $main-30;
    padding: 0 15px;
    @include transition();
    &__list {
      max-width: 1280px;
      margin: 0 auto;
      display: flex;
      padding: 0;
      list-style: none;
      gap: 48px;
      @include response(900px) {
        display: block;
      }
    }
    &__item {
      &.accent {
        .header-nav__link {
          color: $accent;
        }
      }
    }
    &__link {
      padding: 15px 0;
      display: flex;
      align-items: center;
      gap: 6px;
      font-weight: 700;
      color: #000;
      text-transform: uppercase;
      text-decoration: none;
      position: relative;
      svg {
        margin-top: -1px;
        path {
          @include transition();
        }
      }
      @include transition();
      @include hover() {
        color: $accent;
        svg path {
         fill: $accent;
        }
      }
    }
    @include response(900px) {
      position: fixed;
      left: 0;
      top: 99px;
      width: 100%;
      height: 100%;
      background: $main-70;
      padding: 48px;
      display: flex;
      justify-content: center;
      transform: translateX(-100%);
      &.active {
        transform: translateX(0);
      }
    }
  }
  &.fixed {
    .header-nav {
      transform: translateY(-100%);
      &.active{
        transform: translateY(0);
      }
    }
    .nav-btn {
      width: 20px;
    }
  }
}

.search {
  &__inner {
    position: relative;
  }
  &__field {
    width: 514px;
    height: 43px;
    border-radius: 99px;
    background: #fff;
    border: 1px solid #B3BDBC;
    padding: 0 34px 0 16px;
    font-size: 14px;
  }
  &__icon {
    position: absolute;
    right: 16px;
    top: 12px;
    @include transition();
  }
  @include response(900px) {
    display: none;
  }
}

.nav-btn {
  $bar-w: 20px;
  $bar-h: 3px;
  $bar-color: #fff;
  height: 18px;
  cursor: pointer;
  background: transparent;
  border: none;
  padding: 0;
  position: relative;
  width: 0;
  overflow: hidden;
  @include transition();
  b {
    position: absolute;
    border-radius: 3px;
    left: 0;
    width: $bar-w;
    height: $bar-h;
    background-color: $bar-color;
    @include transition();
    &:nth-child(1) {
      top: 0;
    }
    &:nth-child(2) {
      top: calc(50% - 1.5px);
    }
    &:nth-child(3) {
      bottom: 0;
    }
  }
  &.active{
    b:nth-child(1) {
      transform: rotate(45deg);
      transform-origin: 20% 30%;
      top: 5px;
    }
    b:nth-child(2) {
      width: 0;
    }
    b:nth-child(3) {
      transform: rotate(-45deg);
      transform-origin: 15% 66%;
      bottom: 1px;
    }

  }
  @include response(900px) {
    width: 20px;
  }
}